import config from "./config";
import axios from "axios";

const GET = "GET";
const { test_API_URL } = config;

const createApiRequest = (method, route, accessToken, login) => {
    return axios({
        method,
        url: test_API_URL + route,
        headers: {
            Authorization: "Bearer " + accessToken,
            DateInterval: 1,
            "X-API-Version": "2",
            Agent: login
        }
    }).catch(function(error) {
        console.error(error);
    });
};

const api = {
    fetchAgents: accessToken => createApiRequest(GET, "/agents", accessToken),
    get_archives: (accessToken) =>
        createApiRequest(GET, "/get_archives", accessToken),
    fetchChattingTime: (login, accessToken) =>
        createApiRequest(GET, "/chatting", accessToken, login)
};

export default api;
